import { Directive, TemplateRef } from '@angular/core';


/**
 * The `FooterTemplate` directive is used for rendering the list footer content.
 *
 * @example
 * ```ts
 * @@Component({
 * selector: 'my-app',
 * template: `
 *  <kendo-combobox [data]="listItems">
 *    <template footerTemplate>
 *      <h4>Footer template</h4>
 *    </template>
 *  </kendo-combobox>
 * `
 * })
 * class AppComponent {
 *   public listItems: Array<string> = ["Item 1", "Item 2", "Item 3", "Item 4"];
 * }
 * ```
 *
 * For more examples see [Templates]({% slug overview_ddl_kendouiforangular %}#templates)
 */

@Directive({
    selector: '[footerTemplate]'
})
export class FooterTemplateDirective {
    public templateRef: TemplateRef<any>;

    constructor(templateRef: TemplateRef<any>) {
        this.templateRef = templateRef;
    }
}



